
* {
    box-sizing: border-box;
}

*::before,
*::after {
    box-sizing: border-box;
}

body {
    font-family: 'Proxima Nova Rg';
    margin: 0px;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}
header {
    z-index: 9999;
    position: relative;
    padding: 0px 10px;
}
.full-subtop {
    background-color: #FDD44B;

    padding: 0px 10px;
}

.full-subtop p {
    margin-bottom: 0px;
    color: #2C2C2C;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
}

.lean-m {
    text-decoration: underline;
    color: #2C2C2C;
    font-size: 13px;
    margin-left: 10px;
}

.lean-m:hover {
    color: #2C2C2C;
}

.has-search {
    position: relative;
    margin: 0;
}

.has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
    right: 0;
}

.has-search .form-control {
    border-radius: 0;
    font-size: 13px;
    margin: 5px 0px;
    line-height: 17px;
}

.anchor-tag {
    text-decoration: none;
    color: #2C2C2C;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
    margin: 0px 8px;
}

.rightmov {
    justify-content: right;
    margin: 0;
    padding: 0px;
}

.anchor-tag:hover {
    color: #2C2C2C;
    text-decoration: none;
}

.navbar {
    width: 100%;
    background: transparent;
}

.top-bannerimg {
    position: relative;
    top: -90px;
}

.navi-part li a {
    color: #fff;
}

.navi-part li a {
    color: #fff;
    padding: 0px 17px !important;
    font-size: 15px;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
}

.cart-bg {
    background: #FDBDC9;
    padding: 5px;
    border-radius: 100px;
}

.align {
    display: flex;
    width: 100%;
    align-items: center;
}

.active-btn {
    background: #FDD44B;
    color: #0862D9;
    padding: 5px 15px;
    border-radius: 50px;
    font-size: 16px;
    display: inherit;
    margin-right: 9px;
    text-align: center;


    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.active-btn:hover {
    color: #0862D9;
    text-decoration: none;
}

.active-btn img {
    margin-right: 5px;
    width: 15px;
}

.login-btn {
    display: inherit;
    padding: 4px 15px;
    border: 2px solid #FDD44B;
    border-radius: 50px;
    margin-right: 35px;
    text-align: center;
    color: #fff;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.login-btn img {
    margin-right: 5px;
}

.login-btn:hover {
    color: #fff;
    text-decoration: none;
}

.customer-offer {
    background-color: #ba2775;
    margin-top: -90px;
    padding: 50px 0px 35px;
    color: #fff;
}
.nav-tabs-wrapper {
    text-align: center;
    margin: 0px auto;
}

.nav-tabs-wrapper ul {
    background: #fff;
    border-radius: 100px;
    padding: 3px 3px 1px;
    margin-top: 40px;
    box-shadow: 5px 8px #bc006e;
    width: max-content;
}
.nav-tabs-wrapper .nav-tabs .nav-link {
    border: none;
    font-size: 27px;
    color: #B9B9B9;
    padding: 6px 31px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.nav-tabs-wrapper .nav-tabs .nav-link.active {
    background: #295EF8;
    border-radius: 100px;
    padding: 5px 37px;
    color: #fff;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.logo {
    width: 119px;
}
.dropdown-toggle::after {
    content: '';
    background-image: url('../images/Group245.svg');
    border: none;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 100%;
    width: 11px;
    height: 7px;
    vertical-align: middle;
    margin-left: 5px;
    filter: brightness(0) invert(1);
}

.dropdown-menu.show a {
    color: #000;
}
