.heading {
    width: 100%;
    text-align: center;
}

.heading h2 {
    width: 100%;


    font-size: 45px;
    margin-bottom: 15px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.heading p {
    font-size: 18px;
    line-height: 21px;
}



.wmanag-line {
    width: 25%;
    margin-top: 22px;
}


.data-box {
    background: #fff;
    border-radius: 15px;
    padding: 15px;
    box-shadow: 12px 15px #bc006e;
    min-height: 425px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 5px;
}

.g5 {
    font-size: 13px;
    color: #000000;
    margin: 0;
    padding: 0;
    margin-bottom: -6px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
    width: 100%;
}

.gb-m {
    font-size: 42px;
    color: #000000;
    margin: 0;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.gb-m sub {
    bottom: 0;
    font-size: 24px;
}

.shape-gb {
    width: 100%;
    position: relative;
    left: -28px;
    top: -8px;
    width: 249px;
}

.relative {
    position: relative;
}

.compare {
    position: absolute;
    width: 100%;
    left: 0;
    color: #68716B;
    top: 22px;
    text-decoration: underline;
    font-weight: 400;
    font-size: 13px;
    top: 14px;
}

.compare a {
    color: #68716B;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
    font-size: 12px;
}

.dollar-text {
    color: #b92674;
    font-size: 60px;


    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.dollar-text sup {
    font-size: 30px;
    top: -0.8em;
}

.dollar-text sub {
    font-size: 20px;
    bottom: 0em;
}

.points-link p {
    color: #4B4B4D;
    margin-bottom: 3px;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
    font-size: 16px;
}

.points-link {
    margin-bottom: 15px;
}

.btn-detail {
    background-color: #6633B5;
    border-color: #6633B5;
    border-radius: 50px;
    text-align: center;
    margin: 0px auto;
    width: 100%;
    padding: 7px 8px 7px;
    font-size: 18px;
    color: #fff;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
}

.btn-detail:focus {
    border: none;
    box-shadow: none;
}

.servi-m {
    margin: 0;
    text-align: center;
    color: #4B4B4D;
    margin-top: 6px;
    display: block;
    margin: 6px auto;
    margin-bottom: 0;
    font-size: 13px;
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
}

.tab-content.w-100 {
    margin-top: 60px;
}

.menu-icon {
    width: 26px;
    margin-right: 10px;
    margin-top: -13px;
}

.points-link img {
    position: relative;
    top: -3px;
}

.font38 {
    font-size: 38px;
}

.btn.btn-buy {
    background: #ba2775;
    padding: 7px 43px 7px;
    color: #fff;
    position: absolute;
    bottom: 35px;
    left: 35%;
    z-index: 9999 !important;
    cursor: pointer !important;
    border-radius: 50px;
    font-size: 20px;
    text-transform: uppercase;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.bot {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.unlimited {
    width: 100%;
    margin-top: 43px;
    text-align: center;
    font-size: 13px;
}

.framephone {
    background-color: #f7d664;
    color: #000000;
    font-size: 19px;
    border-radius: 50px;
    text-align: center;
    margin: 0px auto;
    padding: 9px 21px 9px;
    display: flex;
    align-items: center;
    margin-top: 15px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.btn.framephone img {
    width: 19px;
    margin-right: 6px;
}

.join-is-easy {
    padding: 50px 0px 0px;
    background-color: #6cc0d2;
    color: #000000;
    text-align: center;
}

.join-is-easy h4 {
    font-size: 17px;
    text-align: center;
    margin-top: 27px;
    line-height: 18px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.joining h2 {
    font-size: 57px;
    font-family: 'Proxima Nova Rg';
    font-weight: bold;
}

.blacksvg {
    width: 47%;
    margin-top: 20px;
    margin-bottom: 27px;
}

.joining p {
    font-weight: 600;
    font-family: 'Proxima Nova Lt';
    font-size: 17px;
    letter-spacing: 0px;
    padding: 0px 16px;
    margin-bottom: 60px;
    line-height: 23px;
}

.right-line .col-md-3 {
    position: relative;
}

.right-line .col-md-3:before {
    content: '';
    position: absolute;
    width: 59px;
    height: 1px;
    background-color: #539aa9;
    top: 27%;
    left: 135px;
}

.joining {
    margin-top: 70px;
}

.right-line img {
    width: 70px;
}

.right-line .col-md-3:last-child:before {
    display: none;
}

.popover-dismiss {
    padding: 0px 0px 0px 5px;
}

.font38 {
    margin-top: 18px;
    position: relative;
    top: -8px;
}
.topm {
    position: relative;
    top: 3px;
}